<template>
  <div class="DoingList">
    <div class="describe">{{ list.message }}</div>
    <div class="ptime">预计耗费时间：{{ time }}</div>
    <div>{{ list.date }}</div>
    <div class="interval">{{ hour }} : {{ minute }} : {{ second }}</div>
  </div>
</template>

<script>
export default {
  name: "doneList",
  data() {
    return {
      hours: 0,
      minutes: 0,
      seconds: 0,
    };
  },
  props: {
    list: Object,
  },
  methods: {
    num(n) {
      return n < 10 ? "0" + n : n;
    }
  },
  computed: {
    time() {
      switch (this.list.standard) {
        case "seconds":
          return this.list.time + "s";
        case "minutes":
          return this.list.time + "min";
        case "hours":
          return this.list.time + "h";
      }
    },
    hour() {
      return this.num(this.hours);
    },
    minute() {
      return this.num(this.minutes);
    },
    second() {
      return this.num(this.seconds)
    }
  },
  created() {
    switch (this.list.standard) {
      case "hours":
        this.hours = this.list.time;
        break;
      case "minutes":
        this.minutes = this.list.time;
        break;
      case "seconds":
        this.seconds = this.list.time;
        break;
    }
    let time = setInterval(()=>{
      if(this.hours == 0 && this.minutes == 0 && this.seconds == 0){
        clearInterval(time);
        this.$store.commit('changeState');
        this.$emit("deleteDoingList",this.list);
        alert("计时结束");
      }else if(this.seconds == 0 && this.minutes != 0){
        this.minutes--;
        this.seconds = 59;
      }else if(this.seconds == 0 && this.minutes == 0 && this.hours != 0){
        this.hours--;
        this.minutes = 59;
        this.seconds = 59;
      }else{
        this.seconds--;
      }
    },1000)
  },
  watch: {
    hour: {
      handler(newVal){
        return this.num(newVal)
      }
    },
    minute: {
      handler(newVal){
        return this.num(newVal);
      }
    },
    second: {
      handler(newVal){
        return this.num(newVal);
      }
    }
  }
};
</script>

<style>
.DoingList {
  display: flex;
  justify-content: space-around;
}
</style>