<template>
  <div>
    <div v-if="doingLists.length == 0">
      <h2>无正在进行的任务</h2>
      <button @click="check" class="toCreate">去创建</button>
    </div>
    <div class="doingLists" v-else>
      <doingList
        v-for="list in doingLists"
        :key="list.message"
        :list="list"
        @deleteDoingList="deleteDoingList"
      ></doingList>
    </div>
  </div>
</template>

<script>
import doingList from "../components/DoingList";
export default {
  name: "doingLists",
  components: {
    doingList,
  },
  data() {
    return {
      doingLists: [],
    };
  },
  beforeRouteEnter(to, from, next) {
    if (to.query.message == undefined) {
      next();
    } else {
      next((vm) => {
        let d = JSON.parse(to.query.date);
        // console.log(d)
        let message = to.query.message,
          time = to.query.time,
          standard = to.query.standard,
          date = new Date(d).format("yyyy-MM-dd hh:mm:ss");
        vm.doingLists.push({
          message,
          time,
          standard,
          date,
        });
      });
    }
  },
  methods: {
    check() {
      this.$router.push("/toDoList");
    },
    deleteDoingList(list) {
      this.doingLists.shift();
      this.$store.commit('addDoneList', list)
    }
  },
};
</script>

<style>
  .toCreate{
    margin-left: 10px;
  }
</style>